*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  scroll-behavior: smooth; }

body {
  margin: 60px 0;
  font-family: "Roboto";
  -webkit-font-smoothing: antialiased;
  text-decoration-skip: objects;
  text-rendering: optimizeLegibility;
  text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  background-color: #f0f0f0; }

a {
  text-decoration: none;
  color: inherit; }

button:not([disabled]),
a[role="button"]:not([disabled]) {
  cursor: pointer; }

.title {
  margin: 0;
  font-size: 18px; }

.is-hidden {
  display: none !important; }

@media (max-width: 1024px) {
  .for-desktop {
    display: none !important; } }

@media (min-width: 1025px) {
  .for-mobile {
    display: none !important; } }

.clearfix {
  overflow: auto; }
  .clearfix::after {
    content: "";
    display: table;
    clear: both; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0); }

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0; }

.container {
  margin: 0 auto 2px;
  padding: 28px 20px;
  min-height: 250px;
  max-width: 662px;
  background-color: #fff; }

.header__inner {
  position: relative;
  display: flex; }

.header__photo {
  width: 139px;
  height: 139px;
  border-radius: 50%;
  object-fit: contain; }

.header__list {
  flex: 1;
  padding-left: 22px;
  font-size: 12px;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  color: #808080; }

.header__item {
  margin-top: 33px;
  border-bottom: 2px solid #000;
  padding-bottom: 7px; }
  .header__item a:hover {
    text-decoration: underline; }
  .header__item:first-child {
    margin: 0; }
  .header__item--contacts {
    display: flex;
    flex-direction: column; }

.header__mail {
  text-transform: none; }

.header__stamp {
  position: absolute;
  right: 6px;
  bottom: 11px; }

.skills__container {
  padding-right: 0;
  min-height: 220px; }

.skills__title {
  margin-top: 4px; }

.skills__list {
  display: flex;
  flex-wrap: wrap;
  margin-top: 22px;
  font-size: 12px;
  color: #808080; }

.skills__item {
  display: inline-block;
  flex-basis: 25%;
  margin-bottom: 38px;
  padding-right: 20px;
  letter-spacing: 1.2px; }
  .skills__item:nth-last-child(-n + 4) {
    margin: 0; }

.checkbox {
  display: inline-block; }
  .checkbox span {
    text-transform: uppercase;
    transition: 0.4s; }
  .checkbox__inner {
    display: flex;
    align-items: center;
    cursor: pointer; }
  .checkbox__icons {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 8px; }
  .checkbox__checkbox {
    width: 19px;
    height: 19px;
    fill: #eee;
    transition: 0.4s; }
  .checkbox__on {
    width: 13px;
    height: 10px;
    position: absolute;
    opacity: 0;
    visibility: hidden;
    transition: 0.4s; }
  .checkbox:hover .checkbox__checkbox {
    fill: #928c8b; }
  .checkbox:hover span {
    color: #000; }

.checkbox__input-checkbox:checked ~ .checkbox__checkbox {
  fill: #00f; }

.checkbox__input-checkbox:checked ~ .checkbox__on {
  opacity: 1;
  visibility: visible; }

input[type="checkbox"] {
  width: 1px;
  height: 1px;
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0); }

.footer__inner {
  display: flex;
  padding: 57px 20px; }

.footer__images {
  position: relative; }

.footer__meter {
  width: 253px;
  height: 127px; }

.footer__arrow {
  width: 55px;
  height: 39px;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-100%) rotate(-32deg);
  transform-origin: 90% 90%;
  transition: 0.4s; }

.footer__level {
  margin: 7px 0 0 69px;
  max-width: 220px;
  line-height: 1.3; }

.footer__counter {
  margin-top: 7px;
  font-weight: 900;
  font-size: 50px;
  color: #0093d7; }

@media (max-width: 768px) {
  .header__inner {
    flex-direction: column;
    align-items: center; }
  .header__list {
    margin-top: 30px;
    padding: 0;
    width: 100%; }
  .skills__item {
    flex-basis: 33.333%; }
  .footer__inner {
    flex-direction: column;
    align-items: center; }
  .footer__level {
    margin: 40px 0 0; } }

@media (max-width: 480px) {
  .header__stamp {
    width: 35%; }
  .skills__item {
    flex-basis: 50%; }
    .skills__item:nth-last-child(-n + 4) {
      margin: 0; }
    .skills__item:nth-last-child(3), .skills__item:nth-last-child(4) {
      margin-bottom: 38px; } }
